import React, { Component } from 'react';
import Popover from 'react-popover';
import * as FontAwesome from 'react-icons/lib/fa';
import NodePicker from '../NodePicker/';
import Option from '../Option/';
import './VisibleOption.css';

class VisibleOption extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: this.props.option.name,
            selector: this.props.option.selector,
            default: this.props.option.default,
            isPopoverOpen: false
        };
    }

    handleSubmit = event => {
        const option = {
            ...this.props.option,
            name: this.state.name,
            selector: this.state.selector,
            default: this.state.default
        };
        if (this.props.onUpdateOption) {
            this.props.onUpdateOption(this.props.option.uid, option);
        }
    };

    handleChange = event => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    };

    togglePopover = e => {
        this.setState({
            isPopoverOpen: !this.state.isPopoverOpen
        });
    };

    renderPopoverContent() {
        var match, instanceID;
        if (this.state.selector) {
            if ((match = this.state.selector.match(/\[instanceID="(\d+)"\]/))) {
                instanceID = parseInt(match[1], 10);
            }
        }

        return (
            <div className="NodePicker__container">
                <NodePicker
                    root={this.props.scene}
                    lastClickEvent={this.props.lastClickEvent}
                    onNodeSelected={this.handleNodeSelected}
                    selectedNode={instanceID}
                />
            </div>
        );
    }

    handleNodeSelected = e => {
        const selector = e ? `[instance="${e.instanceID}"]` : '';
        this.setState(
            {
                selector: selector
            },
            this.handleSubmit.bind(this)
        );
    };

    render() {
        return (
            <Option option={this.props.option} onDeleteOption={this.props.onDeleteOption}>
                <div className="option__row">
                    <label>Name</label>
                    <input
                        type="text"
                        name="name"
                        value={this.state.name}
                        onChange={this.handleChange}
                        onBlur={this.handleSubmit}
                        size="15"
                        className="input --text"
                    />
                </div>
                <div className="option__row">
                    <label>Object</label>
                    <Popover
                        isOpen={this.state.isPopoverOpen}
                        body={this.renderPopoverContent()}
                        onOuterAction={this.togglePopover}
                        enterExitTransitionDurationMs={0}
                    >
                        <button className="button --select" type="button" onClick={this.togglePopover}>
                            {this.props.option.selector
                                ? `${this.props.option.selector}`
                                : 'Select object'}
                            <FontAwesome.FaChevronDown />
                        </button>
                    </Popover>
                </div>
                <div className="option__row">
                    <label>Default</label>
                    <label>
                        <input
                            type="checkbox"
                            name="default"
                            value="true"
                            checked={this.state.default}
                            onChange={this.handleChange}
                            onBlur={this.handleSubmit}
                        />
                        Visible
                    </label>
                </div>
            </Option>
        );
    }
}

export default VisibleOption;



