export default {
    color: {
        label: 'Color',
        description: '',
        defaultState: () => ({
            name: 'Color',
            type: 'color',
            material: '',
            options: [],
            default: '#FFFFFF',
            uid: Date.now()
        }),
        serializeOption: option => {
            let output = {
                name: option.name,
                type: 'color',
                material: option.material,
                default: option.default
            };
            if (option.options.length) {
                output.options = option.options.map(opt => ({
                    ...opt,
                    id: option.uid
                }));
            }
            return output;
        }
    },
    visible: {
        label: 'Toggle',
        description: '',
        defaultState: () => ({
            name: 'Toggle',
            type: 'visible',
            selector: null,
            default: true,
            uid: Date.now()
        }),
        serializeOption: option => ({
            name: option.name,
            type: 'visible',
            selector: option.selector,
            default: option.default
        })
    },
    select: {
        label: 'Select',
        description: '',
        defaultState: () => ({
            name: 'Select',
            type: 'select',
            options: [],
            default: 0,
            uid: Date.now()
        }),
        serializeOption: option => ({
            name: option.name,
            type: 'select',
            options: option.options,
            default: option.default
        })
    },
    texture: {
        label: 'Texture',
        description: '',
        defaultState: () => ({
            name: 'Texture',
            material: '',
            channels: ['AlbedoPBR'],
            type: 'texture',
            options: [],
            default: 0,
            uid: Date.now()
        }),
        serializeOption: option => ({
            name: option.name,
            material: option.material,
            channels: option.channels,
            type: 'texture',
            options: option.options,
            default: option.default
        })
    }
};



