import React, { Component } from 'react';
import * as FontAwesome from 'react-icons/lib/fa';
import './Option.css';

const optionTypeNames = {
    color: 'Color',
    visible: 'Toggle',
    select: 'Select',
    texture: 'Texture'
};

class Option extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isCollapsed: false
        };
    }

    deleteOption = event => {
        const uid = this.props.option.uid;
        if (this.props.onDeleteOption && typeof this.props.onDeleteOption === 'function') {
            this.props.onDeleteOption(uid);
        }
    };

    toggleCollapsed = event => {
        this.setState({
            isCollapsed: !this.state.isCollapsed
        });
    };

    render() {
        let option = this.props.option;
        const optionType = option.type;
        let optionTypeName = optionTypeNames[optionType]
            ? optionTypeNames[optionType]
            : 'Unknown Option';
        const optionClassNames = [
            'option',
            optionType,
            this.state.isCollapsed ? '--collapsed' : ''
        ].join(' ');
        return (
            <div className={optionClassNames}>
                <div className="option__header">
                    <button
                        className="option__collapse button --icon"
                        onClick={this.toggleCollapsed}
                    >
                        <FontAwesome.FaCaretDown />
                    </button>
                    <b>{optionTypeName}</b>
                    <button
                        className="button --icon"
                        onClick={this.deleteOption}
                        title="Remove option"
                    >
                        <FontAwesome.FaTrash />
                    </button>
                </div>
                <div className="option__content">{this.props.children}</div>
            </div>
        );
    }
}

export default Option;



