import React, { Component } from 'react';
import { rgbToHex, linearToSrgb } from '../../utils/ColorSpace';
import Option from '../Option/';
import MaterialPicker from '../MaterialPicker/';
import * as FontAwesome from 'react-icons/lib/fa';
import './ColorOption.css';

function getHexColorFromMaterial(material) {
    var color;

    if (
        material.channels.AlbedoPBR.enable === true &&
        material.channels.AlbedoPBR.color !== undefined
    ) {
        color = material.channels.AlbedoPBR.color;
    } else if (
        material.channels.DiffusePBR.enable === true &&
        material.channels.DiffusePBR.color !== undefined
    ) {
        color = material.channels.DiffusePBR.color;
    } else if (
        material.channels.DiffuseColor.enable === true &&
        material.channels.DiffuseColor.color !== undefined
    ) {
        color = material.channels.DiffuseColor.color;
    } else {
        color = [1, 1, 1];
    }

    var srgb = linearToSrgb(color);
    var hex = rgbToHex({
        r: srgb[0],
        g: srgb[1],
        b: srgb[2]
    });
    return hex;
}

class ColorOption extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: this.props.option.name,
            materialName: this.props.option.material,
            options: this.props.option.options,
            default: this.props.option.default
        };
    }

    handleAddPredefinedColorClicked = event => {
        let newOptions = [
            ...this.state.options,
            {
                name: 'white',
                color: '#FFFFFF',
                id: Date.now()
            }
        ];
        this.setState({
            options: newOptions
        });
    };

    handleRemovePredefinedColorClicked = event => {
        var index = event.currentTarget.getAttribute('data-index');
        var newOptions = Array.from(this.state.options);
        newOptions.splice(index, 1);
        this.setState({
            options: newOptions
        });
    };

    handleSubmit = event => {
        const option = {
            ...this.props.option,
            name: this.state.name,
            material: this.state.materialName,
            default: this.state.default,
            options: this.state.options
        };

        if (this.props.onUpdateOption) {
            this.props.onUpdateOption(this.props.option.uid, option);
        }
    };

    handleChange = event => {
        const target = event.target;
        let value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        if (name === 'default') {
            value = value.toUpperCase();
        }

        this.setState({
            [name]: value
        });
    };

    handleColorChange = event => {
        const target = event.target;
        const index = parseInt(event.target.getAttribute('data-index'), 10);
        const field = target.name;

        var newOptions = this.state.options.map((option, i) => {
            if (i === index) {
                if (option.name === 'color') {
                    option.color = option.color.toUpperCase();
                }
                return {
                    name: option.name,
                    color: option.color,
                    id: option.id,
                    [field]: target.value
                };
            } else {
                return option;
            }
        });
        this.setState({
            options: newOptions
        });
    };

    handleSetDefaultClicked = event => {
        const target = event.currentTarget;
        const index = parseInt(target.getAttribute('data-index'), 10);
        if (this.state.options && this.state.options[index]) {
            const defaultColor = this.state.options[index].color;
            this.setState({
                default: defaultColor
            });
        }
    };

    renderPalette() {
        if (this.state.options.length === 0) {
            return <div className="option__row--color">Any color</div>;
        }

        return this.state.options.map((color, index) => {
            return (
                <div
                    key={color.id}
                    data-index={index}
                    onChange={this.handleColorChange}
                    onBlur={this.handleSubmit}
                    className="option__row--color"
                >
                    <input
                        type="color"
                        name="color"
                        defaultValue={color.color}
                        data-index={index}
                        placeholder="#FFFFFF"
                    />
                    <input
                        type="text"
                        name="name"
                        defaultValue={color.name}
                        data-index={index}
                        placeholder="Name"
                        className="input --text"
                        title="Color name"
                        size="8"
                    />
                    <button
                        type="button"
                        data-index={index}
                        onClick={this.handleSetDefaultClicked}
                        className="button --icon"
                        title="Use as default color"
                    >
                        {color.color !== this.state.default ? (
                            <FontAwesome.FaStarO />
                        ) : (
                            <FontAwesome.FaStar />
                        )}
                    </button>
                    <button
                        type="button"
                        data-index={index}
                        onClick={this.handleRemovePredefinedColorClicked}
                        className="button --icon"
                        title="Remove color"
                    >
                        <FontAwesome.FaTrash />
                    </button>
                </div>
            );
        });
    }

    render() {
        return (
            <Option option={this.props.option} onDeleteOption={this.props.onDeleteOption}>
                <div className="option__row">
                    <label>Name</label>
                    <input
                        type="text"
                        name="name"
                        value={this.state.name}
                        onChange={this.handleChange}
                        onBlur={this.handleSubmit}
                        size="15"
                        className="input --text"
                    />
                </div>
                <div className="option__row">
                    <label>Material</label>
                    <MaterialPicker
                        lastClickEvent={this.props.lastClickEvent}
                        materials={this.props.materials}
                        onMaterialSelected={material => {
                            var hexColor = getHexColorFromMaterial(material);
                            this.setState(
                                {
                                    materialName: material.name,
                                    default: hexColor
                                },
                                this.handleSubmit.bind(this)
                            );
                        }}
                    />
                </div>
                <div className="option__row">
                    <label>Default color</label>
                    <input
                        type="color"
                        name="default"
                        onChange={this.handleChange}
                        onBlur={this.handleSubmit}
                        value={this.state.default}
                    />
                </div>
                <div className="option__row">
                    <label>Allowed colors</label>
                    <div>
                        <div>{this.renderPalette()}</div>
                        <button onClick={this.handleAddPredefinedColorClicked} className="button">
                            Add color
                        </button>
                    </div>
                </div>
            </Option>
        );
    }
}

export default ColorOption;



