import React, { Component } from 'react';
import "./Viewer.css";

var doc;

function _renderGraphNode(doc, node) {
    var newNode = doc.createElement(node.type);
    newNode.setAttribute('instance', node.instanceID);
    if (node.name) {
        newNode.setAttribute('name', node.name);
    }

    if (node.children && node.children.length) {
        for (var i = 0, l = node.children.length; i < l; i++) {
            newNode.appendChild(_renderGraphNode(doc, node.children[i]));
        }
    }
    return newNode;
}

class Viewer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            urlid: null
        };
        this.api = null;
    }

    shouldComponentUpdate(nextProps, nextState) {
        return nextProps.urlid !== this.props.urlid;
    }

    getMaterials() {
        return new Promise((resolve, reject) => {
            if (!this.api) {
                reject('Viewer API is not ready');
            }
            this.api.getMaterialList(function(err, materials) {
                if (err) {
                    reject(err);
                }
                resolve(materials);
            });
        });
    }

    getScene() {
        return new Promise((resolve, reject) => {
            if (!this.api) {
                reject('Viewer API is not ready');
            }
            this.api.getSceneGraph((err, result) => {
                if (err) {
                    reject(err);
                }
                doc = document.implementation.createDocument('', '', null);
                doc.appendChild(_renderGraphNode(doc, result));
                resolve(doc);
            });
        });
    }

    onViewerReady() {
        this.api.pause();
        var promises = [this.getMaterials(), this.getScene()];

        Promise.all(promises)
            .then(result => {
                if (this.props.onModelLoaded) {
                    this.props.onModelLoaded(result[0], result[1]);
                }
            })
            .catch(() => {
                console.log('onViewerReady error');
            });
    }

    onViewerClick(e) {
        if (this.props.onViewerClicked) {
            this.props.onViewerClicked(e);
        }
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        if (nextProps.urlid !== this.state.urlid) {
            this.setState({
                urlid: nextProps.urlid
            });
            var client = new window.Sketchfab(this.iframe);
            client.init(nextProps.urlid, {
                success: api => {
                    this.api = api;
                    api.start();
                    api.addEventListener('viewerready', this.onViewerReady.bind(this));
                    api.addEventListener('click', this.onViewerClick.bind(this), {
                        picking: 'slow'
                    });
                },
                error: function onError() {
                    console.log('Viewer error');
                }
            });
        }
    }

    render() {
        return (
            <div className="sketchfab-viewer">
                <iframe
                    ref={iframe => {
                        this.iframe = iframe;
                    }}
                    src="about:blank"
                    allowFullScreen="allowfullscreen"
                    title="Sketchfab Viewer"
                    width="640"
                    height="400"
                />
            </div>
        );
    }
}

export default Viewer;



