import React, { Component } from 'react';
import MaterialPicker from '../MaterialPicker/';
import Option from '../Option/';
import * as FontAwesome from 'react-icons/lib/fa';
import "./TextureOption.css";

class TextureOption extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: this.props.option.name,
            material: this.props.option.material,
            channels: this.props.option.channels,
            options: this.props.option.options,
            default: 0
        };
    }

    removeTexture = event => {
        var index = event.currentTarget.getAttribute('data-index');
        var newOptions = Array.from(this.state.options);
        newOptions.splice(index, 1);
        this.setState({
            options: newOptions
        });
    };

    handleAddTextureClick = event => {
        var newOptions = [
            ...this.state.options,
            {
                name: '',
                url: '',
                id: Date.now()
            }
        ];
        this.setState({
            options: newOptions
        });
    };

    handleTextureChange = event => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        const index = parseInt(event.target.getAttribute('data-index'), 10);
        var newOptions = this.state.options.map((option, i) => {
            if (i === index) {
                let newOption = {
                    ...option,
                    [name]: value
                };
                return newOption;
            } else {
                return option;
            }
        });
        this.setState({
            options: newOptions
        });
    };

    handleChange = event => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    };

    handleSubmit = event => {
        const option = {
            ...this.props.option,
            name: this.state.name,
            material: this.state.material,
            channels: this.state.channels,
            options: this.state.options.map(option => {
                return {
                    name: option.name,
                    url: option.url
                };
            }),
            default: this.state.default
        };

        if (this.props.onUpdateOption) {
            this.props.onUpdateOption(this.props.option.uid, option);
        } else {
            console.log('onUpdateOption', option);
        }
    };

    handleDefaultclicked = e => {
        var target = e.currentTarget;
        var index = parseInt(target.getAttribute('data-index'), 10);
        this.setState(
            {
                default: index
            },
            this.handleSubmit
        );
    };

    renderOptions() {
        return this.state.options.map((option, index) => {
            return (
                <div key={option.id} className="texture__texture">
                    <input
                        name="url"
                        type="text"
                        value={option.url}
                        data-index={index}
                        placeholder="https://"
                        onChange={this.handleTextureChange}
                        onBlur={this.handleSubmit}
                        title="Image URL (must be CORS-enabled)"
                        size="18"
                        className="input --text"
                    />
                    <input
                        name="name"
                        type="text"
                        value={option.name}
                        data-index={index}
                        placeholder="Name"
                        onChange={this.handleTextureChange}
                        onBlur={this.handleSubmit}
                        title="Name"
                        size="18"
                        className="input --text"
                    />
                    <button
                        data-index={index}
                        onClick={this.handleDefaultclicked}
                        className="button --icon"
                        title="Make default"
                    >
                        {index !== this.state.default ? (
                            <FontAwesome.FaStarO />
                        ) : (
                            <FontAwesome.FaStar />
                        )}
                    </button>
                    <button
                        onClick={this.removeTexture}
                        data-index={index}
                        className="button --icon"
                        title="Remove texture"
                    >
                        <FontAwesome.FaTrash />
                    </button>
                </div>
            );
        });
    }

    render() {
        return (
            <Option option={this.props.option} onDeleteOption={this.props.onDeleteOption}>
                <div className="option__row">
                    <label>Name</label>
                    <input
                        type="text"
                        name="name"
                        value={this.state.name}
                        onChange={this.handleChange}
                        onBlur={this.handleSubmit}
                        size="15"
                        className="input --text"
                    />
                </div>
                <div className="option__row">
                    <label>Material</label>
                    <MaterialPicker
                        lastClickEvent={this.props.lastClickEvent}
                        materials={this.props.materials}
                        onMaterialSelected={material => {
                            this.setState({
                                material: material.name
                            });
                        }}
                    />
                </div>
                <div style={{ display: 'none' }}>
                    <label>Channel</label>
                    {this.state.channels.join(',')}
                </div>
                <div className="option__row">
                    <label>Textures</label>
                    <div>
                        {this.renderOptions()}
                        <button
                            type="button"
                            onClick={this.handleAddTextureClick}
                            className="button"
                        >
                            Add texture
                        </button>
                    </div>
                </div>
            </Option>
        );
    }
}

export default TextureOption;



