import React, { Component } from 'react';
import Popover from 'react-popover';
import NodePicker from '../NodePicker/';
import Option from '../Option/';
import * as FontAwesome from 'react-icons/lib/fa';
import './SelectOption.css';

class SelectOption extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: this.props.option.name,
            options: this.props.option.options,
            default: 0,
            isPopoverOpen: false
        };
    }

    removeObject = event => {
        var index = event.currentTarget.getAttribute('data-index');
        var newOptions = Array.from(this.state.options);
        newOptions.splice(index, 1);
        this.setState({
            options: newOptions
        });
    };

    handleObjectNameChange = event => {
        const target = event.target;
        var index = parseInt(target.getAttribute('data-index'), 10);
        var newOptions = this.state.options.map((option, i) => {
            if (i === index) {
                return {
                    name: target.value,
                    selector: option.selector,
                    id: option.id
                };
            } else {
                return option;
            }
        });
        this.setState({
            options: newOptions
        });
    };

    handleChange = event => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    };

    handleSubmit = event => {
        const option = {
            ...this.props.option,
            name: this.state.name,
            options: this.state.options.map(option => {
                return {
                    name: option.name,
                    selector: option.selector
                };
            }),
            default: this.state.default
        };

        if (this.props.onUpdateOption) {
            this.props.onUpdateOption(this.props.option.uid, option);
        }
    };

    togglePopover = e => {
        this.setState({
            isPopoverOpen: !this.state.isPopoverOpen
        });
    };

    renderPopoverContent() {
        return (
            <div className="NodePicker__container">
                <NodePicker
                    root={this.props.scene}
                    lastClickEvent={this.props.lastClickEvent}
                    onNodeSelected={this.handleNodeSelected}
                />
            </div>
        );
    }

    handleNodeSelected = e => {
        const selector = e ? `[instance="${e.instanceID}"]` : '';
        var newOptions = [
            ...this.state.options,
            {
                selector: selector,
                name: e.name ? e.name : 'Object name',
                id: Date.now()
            }
        ];

        this.setState(
            {
                options: newOptions,
                isPopoverOpen: false
            },
            this.handleSubmit
        );
    };

    handleDefaultclicked = e => {
        var target = e.currentTarget;
        var index = parseInt(target.getAttribute('data-index'), 10);
        this.setState(
            {
                default: index
            },
            this.handleSubmit
        );
    };

    renderOptions() {
        return this.state.options.map((option, index) => {
            return (
                <div key={option.id} className="select_object">
                    <span title="Selector">{option.selector}</span>
                    <div>
                        <input
                            type="text"
                            value={option.name}
                            data-index={index}
                            placeholder="Name"
                            onChange={this.handleObjectNameChange}
                            onBlur={this.handleSubmit}
                            className="input --text"
                            size="11"
                            title="Name"
                        />
                        <button
                            data-index={index}
                            onClick={this.handleDefaultclicked}
                            className="button --icon"
                            title="Make default"
                        >
                            {index !== this.state.default ? (
                                <FontAwesome.FaStarO />
                            ) : (
                                <FontAwesome.FaStar />
                            )}
                        </button>
                        <button
                            onClick={this.removeObject}
                            data-index={index}
                            className="button --icon"
                            title="Remove object"
                        >
                            <FontAwesome.FaTrash />
                        </button>
                    </div>
                </div>
            );
        });
    }

    render() {
        return (
            <Option option={this.props.option} onDeleteOption={this.props.onDeleteOption}>
                <div className="option__row">
                    <label>Name</label>
                    <input
                        type="text"
                        name="name"
                        value={this.state.name}
                        onChange={this.handleChange}
                        onBlur={this.handleSubmit}
                        size="15"
                        className="input --text"
                    />
                </div>
                <div className="option__row">
                    <label>Objects</label>
                    <div>
                        {this.renderOptions()}
                        <Popover
                            isOpen={this.state.isPopoverOpen}
                            body={this.renderPopoverContent()}
                            onOuterAction={this.togglePopover}
                            enterExitTransitionDurationMs={0}
                        >
                            <button type="button" onClick={this.togglePopover} className="button">
                                Add object
                            </button>
                        </Popover>
                    </div>
                </div>
            </Option>
        );
    }
}

export default SelectOption;



