import React, { Component } from 'react';
import * as FontAwesome from 'react-icons/lib/fa';
import './MaterialPicker.css';

class MaterialPicker extends Component {
    constructor(props) {
        super(props);
        this.state = {
            materialId: this.props.materialId,
            isWaitingClick: false
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (
            this.state.isWaitingClick &&
            this.props.lastClickEvent &&
            this.props.lastClickEvent.createdAt > this.state.isWaitingClick &&
            this.props.lastClickEvent.material
        ) {
            this.setState(
                {
                    isWaitingClick: false,
                    materialId: this.props.lastClickEvent.material.id
                },
                this.handleMaterialSelected
            );
        }
    }

    handleMaterialSelected = () => {
        if (this.props.onMaterialSelected) {
            var selectedMaterial = this.props.materials
                .filter(material => material.id === this.state.materialId)
                .pop();
            if (selectedMaterial) {
                this.props.onMaterialSelected(selectedMaterial);
            }
        }
    };

    handlePickClick = e => {
        this.setState({
            isWaitingClick: this.state.isWaitingClick ? false : Date.now()
        });
    };

    handleChange = event => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState(
            {
                [name]: value
            },
            this.handleMaterialSelected
        );
    };

    render() {
        return (
            <div className="material-picker">
                <select
                    name="materialId"
                    onChange={this.handleChange}
                    value={this.state.materialId}
                    className="select"
                >
                    <option key="empty" value="">
                        Select a material
                    </option>
                    {this.props.materials.map(material => {
                        return (
                            <option key={material.id} value={material.id}>
                                {material.name}
                            </option>
                        );
                    })}
                </select>
                <button onClick={this.handlePickClick} title="Select material in viewer" className={
                            this.state.isWaitingClick
                                ? 'MaterialPicker__pick active'
                                : 'MaterialPicker__pick'
                        }>
                    <FontAwesome.FaEyedropper />
                </button>
            </div>
        );
    }
}

export default MaterialPicker;



