import React, { Component } from 'react';
import Modal from 'react-modal';
import * as FontAwesome from 'react-icons/lib/fa';
import './PopupAbout.css';

class PopupAbout extends Component {
    render() {
        return (
            <Modal
                {...this.props}
                className="PopupAboutModal"
                overlayClassName="PopupAboutModelOverlay"
            >
                <div className="PopupAbout">
                    <div className="PopupAbout__header">
                        <h2>About Sketchfab Configurator Studio</h2>
                        <button onClick={this.props.onRequestClose} title="Close">
                            <FontAwesome.FaClose />
                        </button>
                    </div>
                    <div className="PopupAbout__content">
                        <p>
                            <a
                                href="https://sketchfab.com/"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                Sketchfab
                            </a>{' '}
                            is the leader in 3D visualization and the largest community of 3D
                            creators in the world.{' '}
                            <a
                                href="https://sketchfab.com/3d-configurators"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                Learn about our unique approach
                            </a>{' '}
                            to custom product configurators or{' '}
                            <a
                                href="https://sketchfab.com/3d-configurators#contact"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                get in touch
                            </a>{' '}
                            to talk to a 3D expert.
                        </p>
                    </div>
                </div>
            </Modal>
        );
    }
}
export default PopupAbout;



