import React, { Component } from 'react';
import ColorOption from '../ColorOption/';
import SelectOption from '../SelectOption/';
import VisibleOption from '../VisibleOption/';
import TextureOption from '../TextureOption/';
import './OptionList.css';

class OptionList extends Component {
    renderOptions() {
        return this.props.options.map((option, i) => {
            switch (option.type) {
                case 'color':
                    return (
                        <ColorOption
                            option={option}
                            materials={this.props.materials}
                            key={option.uid}
                            onDeleteOption={this.props.onDeleteOption}
                            onUpdateOption={this.props.onUpdateOption}
                            lastClickEvent={this.props.lastClickEvent}
                        />
                    );
                case 'visible':
                    return (
                        <VisibleOption
                            option={option}
                            key={option.uid}
                            onDeleteOption={this.props.onDeleteOption}
                            onUpdateOption={this.props.onUpdateOption}
                            lastClickEvent={this.props.lastClickEvent}
                            scene={this.props.scene}
                        />
                    );
                case 'select':
                    return (
                        <SelectOption
                            option={option}
                            key={option.uid}
                            onDeleteOption={this.props.onDeleteOption}
                            onUpdateOption={this.props.onUpdateOption}
                            lastClickEvent={this.props.lastClickEvent}
                            scene={this.props.scene}
                        />
                    );
                case 'texture':
                    return (
                        <TextureOption
                            option={option}
                            key={option.uid}
                            onDeleteOption={this.props.onDeleteOption}
                            onUpdateOption={this.props.onUpdateOption}
                            lastClickEvent={this.props.lastClickEvent}
                            materials={this.props.materials}
                        />
                    );
                default:
                    return null;
            }
        });
    }

    renderEmptyState() {
        return <div className="options-list__empty">Start by adding an option to your model</div>;
    }

    render() {
        const viewerReady = this.props.model && this.props.materials && this.props.scene;
        const optionsComponents = this.renderOptions();

        if (optionsComponents.length) {
            return <div className="options-list">{optionsComponents}</div>;
        } else if (viewerReady) {
            return this.renderEmptyState();
        }
        return null;
    }
}

export default OptionList;



