var gamma = 2.4;

export const linearToSrgb1 = function(c) {
    var v = 0.0;
    if (c < 0.0031308) {
        if (c > 0.0) v = c * 12.92;
    } else {
        v = 1.055 * Math.pow(c, 1.0 / gamma) - 0.055;
    }
    return v;
};

export const srgbToLinear1 = function(c) {
    var v = 0.0;
    if (c < 0.04045) {
        if (c >= 0.0) v = c * (1.0 / 12.92);
    } else {
        v = Math.pow((c + 0.055) * (1.0 / 1.055), gamma);
    }
    return v;
};

export const linearToSrgb = function(c, out) {
    var col = out || new Array(c.length);
    col[0] = linearToSrgb1(c[0]);
    col[1] = linearToSrgb1(c[1]);
    col[2] = linearToSrgb1(c[2]);
    if (col.length > 3 && c.length > 3) col[3] = c[3];
    return col;
};

export const srgbToLinear = function(c, out) {
    var col = out || new Array(c.length);
    col[0] = srgbToLinear1(c[0]);
    col[1] = srgbToLinear1(c[1]);
    col[2] = srgbToLinear1(c[2]);
    if (col.length > 3 && c.length > 3) col[3] = c[3];
    return col;
};

export const encodeRGBM = function(color, range, rgbm) {
    rgbm[3] = Math.min(1.0, Math.max(color[0] / range, rgbm[1] / range, rgbm[2] / range, 1e-6));
    rgbm[3] = Math.ceil(rgbm[3] * 255.0) / 255.0;
    var mul = 1.0 / (range * rgbm[3]);
    rgbm[0] = color[0] * mul;
    rgbm[1] = color[1] * mul;
    rgbm[2] = color[2] * mul;
    return rgbm;
};

export const decodeRGBM = function(rgbm, range, color) {
    var mul = range * rgbm[3];
    color[0] = mul * color[0];
    color[1] = mul * color[1];
    color[2] = mul * color[2];
    return color;
};

export const rgbToHex = function(color) {
    var rounded = {
        r: Math.round(color.r * 255),
        g: Math.round(color.g * 255),
        b: Math.round(color.b * 255)
    };

    // Conversion from {r:255,g:255,b:255} to "#FFFFFF"
    // The (0x1000000 | [...]).substr(1) is to force 0-padding (ie. #0000FF instead of #FF)
    var hex =
        '#' + (0x1000000 | rounded.b | (rounded.g << 8) | (rounded.r << 16)).toString(16).substr(1);

    return hex;
};

const isHexadecimalValue = str => /^[0-9a-f]{3}([0-9a-f]{3})?$/i.test(str);

export const parseUrlColor = uriParam => {
    const color = decodeURI(uriParam);
    return isHexadecimalValue(color) ? `#${color}` : color;
};

export default {
    linearToSrgb1,
    srgbToLinear1,
    linearToSrgb,
    srgbToLinear,
    encodeRGBM,
    decodeRGBM,
    rgbToHex,
    parseUrlColor
};



