const VIEWER_API_URL = 'https://static.sketchfab.com/api/sketchfab-viewer-1.3.0.js';
const TEMPLATES = {
    'index.html': 'templates/index.html',
    'SketchfabConfigurator-1.0.6.js': 'templates/SketchfabConfigurator-1.0.6.js',
    'styles.css': 'templates/styles.css'
};

async function addFile(zip, writer, file) {
    return new Promise((resolve, reject) => {
        writer.add(file.filename, new zip.TextReader(file.content), resolve, () => {}, {
            directory: file.isDirectory
        });
    });
}

class ZipExporter {
    export(jsonConfig) {
        return new Promise((resolve, reject) => {
            this.fetchAssets(jsonConfig)
                .then(files => {
                    this.generateZip(files)
                        .then(resolve)
                        .catch(reject);
                })
                .catch(reject);
        });
    }

    fetchAssets(jsonConfig) {
        var filenames = Object.keys(TEMPLATES);
        var promises = filenames.map(filename => {
            return fetch(TEMPLATES[filename])
                .then(response => response.text())
                .then(content => {
                    return {
                        filename: filename,
                        content: content
                            .replace('{{ VIEWER_API_URL }}', VIEWER_API_URL)
                            .replace('{{ JSON_CONFIG }}', jsonConfig)
                    };
                });
        });
        return Promise.all(promises);
    }

    generateZip(files) {
        return new Promise((resolve, reject) => {
            const zip = window.zip;
            zip.createWriter(
                new zip.BlobWriter('application/zip'),
                async writer => {
                    try {
                        // important: files must be added sequentially
                        for (var i = 0, l = files.length; i < l; i++) {
                            await addFile(zip, writer, files[i]);
                        }
                    } catch (e) {
                        throw new Error('Cannot add files: ' + e.message);
                    }
                    writer.close(blob => {
                        var url = window.URL.createObjectURL(blob);
                        resolve(url);
                    });
                },
                reject
            );
        });
    }
}

export default ZipExporter;



