import React, { Component } from 'react';
import './NodePicker.css';
import * as FontAwesome from 'react-icons/lib/fa';

class NodePicker extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedNode: this.props.selectedNode,
            isWaitingClick: false
        };
        this.selectedRef = React.createRef();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (
            this.state.isWaitingClick &&
            this.props.lastClickEvent &&
            this.props.lastClickEvent.createdAt > this.state.isWaitingClick &&
            this.props.lastClickEvent.instanceID
        ) {
            this.setState(
                {
                    isWaitingClick: false,
                    selectedNode: this.props.lastClickEvent.instanceID
                },
                this.handleNodeSelected
            );
        }

        if (this.selectedRef && this.selectedRef.current) {
            this.selectedRef.current.scrollIntoView({ behavior: 'smooth' });
        }
    }

    handleNodeClick = e => {
        e.stopPropagation();
        const target = e.currentTarget;
        let nodeId = parseInt(target.getAttribute('data-id'), 10);

        // Unselection
        if (nodeId === this.state.selectedNode) {
            nodeId = null;
        }

        this.setState(
            {
                selectedNode: nodeId
            },
            this.handleNodeSelected
        );
    };

    handleNodeSelected = () => {
        let selectedNode = null;

        if (this.state.selectedNode !== null) {
            var node = this._getNodeById(this.state.selectedNode);
            selectedNode = {
                instanceID: parseInt(node.getAttribute('instance'), 10),
                name: node.getAttribute('name'),
                type: node.nodeName ? node.nodeName : ''
            };
        }

        if (this.props.onNodeSelected) {
            this.props.onNodeSelected(selectedNode);
        }
    };

    handlePickClick = e => {
        this.setState({
            isWaitingClick: this.state.isWaitingClick ? false : Date.now()
        });
    };

    _getNodeById(id) {
        const selector = `[instance="${id}"]`;
        var node = Array.from(this.props.root.querySelectorAll(selector)).pop();
        return node;
    }

    renderNodeSubtree(node) {
        if (!node) {
            return null;
        }

        let childNodes = Array.from(node.childNodes);
        let children = childNodes.map(child => {
            return this.renderNodeSubtree(child);
        });

        const instanceID = parseInt(node.getAttribute('instance'), 10);
        const nodeType = node.nodeName;
        const nodeName = node.getAttribute('name') ? node.getAttribute('name') : nodeType;
        let icon;
        switch (nodeType) {
            case 'Group':
                icon = <FontAwesome.FaFolderO />;
                break;
            case 'MatrixTransform':
                icon = <FontAwesome.FaArrowsAlt />;
                break;
            case 'Geometry':
                icon = <FontAwesome.FaCube />;
                break;
            default:
                icon = null;
                break;
        }

        return (
            <li key={`instance-id-${instanceID}`}>
                <span
                    data-type={node.nodeName}
                    data-id={instanceID}
                    onClick={this.handleNodeClick}
                    className={
                        this.state.selectedNode === instanceID
                            ? 'NodePicker__node selected'
                            : 'NodePicker__node '
                    }
                    ref={this.state.selectedNode === instanceID ? this.selectedRef : null}
                    title={`${nodeType} - ${nodeName}`}
                >
                    {icon} {nodeName}
                </span>
                {children.length ? <ul>{children}</ul> : null}
            </li>
        );
    }

    render() {
        return (
            <div className="NodePicker">
                <div>
                    <button
                        onClick={this.handlePickClick}
                        title="Select object in viewer"
                        className={
                            this.state.isWaitingClick
                                ? 'NodePicker__pick button active'
                                : 'NodePicker__pick button'
                        }
                    >
                        <FontAwesome.FaEyedropper /> Select in viewer
                    </button>
                </div>
                <ul>
                    {this.props.root &&
                        this.renderNodeSubtree(
                            this.props.root.documentElement,
                            this.handleNodeClick,
                            this.state.selectedNode
                        )}
                </ul>
            </div>
        );
    }
}

export default NodePicker;



